document.addEventListener('DOMContentLoaded', () => {
  const newsContainer = document.getElementById('news-list');
  const loadMoreButton = document.getElementById('load-more'); // Seleziona il bottone

  // Verifica se il bottone è stato trovato correttamente
  console.log(loadMoreButton); // Questo ti aiuterà a capire se il bottone è stato trovato

  if (!loadMoreButton) {
    return; // Uscita anticipata se il bottone non è trovato
  }

  let newsIds = [];
  let currentNewsIndex = 0;

  // Funzione per recuperare le ultime notizie con gestione degli errori
  async function fetchLatestNews() {
    try {
      const res = await fetch('https://hacker-news.firebaseio.com/v0/newstories.json?print=pretty');
      if (!res.ok) throw new Error('Errore nella fetch delle ultime notizie');
      const data = await res.json();
      newsIds = data; // Salviamo tutti gli ID in newsIds
      displayNews(); // Mostriamo i primi 10 articoli
    } catch (error) {
      console.error('Errore durante il recupero delle notizie:', error);
      alert('Si è verificato un errore nel recuperare le notizie. Riprova più tardi.');
    }
  }

  // Funzione per recuperare i dettagli di una singola notizia
  async function fetchNewsDetails(newsId) {
    try {
      const res = await fetch(`https://hacker-news.firebaseio.com/v0/item/${newsId}.json?print=pretty`);
      if (!res.ok) throw new Error('Errore nella fetch dei dettagli della notizia');
      const data = await res.json();
      return data;
    } catch (error) {
      console.error('Errore durante il recupero dei dettagli della notizia:', error);
      return null; // Restituiamo null se c'è un errore
    }
  }

  // Funzione per visualizzare le notizie
  async function displayNews() {
    for (let i = currentNewsIndex; i < currentNewsIndex + 10; i++) {
      if (newsIds[i]) {
        const newsData = await fetchNewsDetails(newsIds[i]);
        if (!newsData) continue; // Se non ci sono dati validi, passiamo alla notizia successiva

        const newsItem = document.createElement('div');
        newsItem.classList.add('news-item');
        const formattedDate = new Date(newsData.time * 1000).toLocaleString(); // Convertiamo il timestamp

        newsItem.innerHTML = `
          <h3><a href="${newsData.url}" target="_blank">${newsData.title}</a></h3>
          <p><strong>Data:</strong> ${formattedDate}</p>
          <p><strong>Link:</strong> <a href="${newsData.url}" target="_blank">${newsData.url}</a></p>
        `;
        newsContainer.appendChild(newsItem);
      }
    }

    currentNewsIndex += 10; // Aggiorniamo l'indice per caricare le news successive
  }

  // Funzione per caricare più notizie quando l'utente clicca su "Load More"
  function loadMoreNews() {
    console.log("Bottone Load More cliccato!");
    // Carichiamo i successivi 10 articoli solo se ce ne sono
    if (newsIds.length > currentNewsIndex) {
      displayNews();
    } else {
      alert('No more news to load.');
    }
  }

  // Iniziamo a recuperare le notizie appena la pagina viene caricata
  fetchLatestNews();

  // Aggiungi l'evento al bottone "Load More"
  loadMoreButton.addEventListener('click', loadMoreNews);
});
